import React, { useState, useEffect } from "react";
import axios from "axios";
import { HashRouter as Router } from "react-router-dom";
import { useRouter } from "./router";
import app from "./fbConfig";

function App() {
  const [currentUser, setCurrentUser] = useState(null);

  const getActualuUserData = async () => {
    await app.auth().onAuthStateChanged(setCurrentUser);

    if (currentUser) {
      console.log(currentUser.providerData[0].uid);
      const userRepo = await axios.get(
        `https://api.github.com/user/${currentUser.providerData[0].uid}`
      );
      console.log("userRepo", userRepo.data.login);
      const getUserBranch = await axios.get(
        `https://api.github.com/repos/${userRepo.data.login}/todo_on_react-native/branches`
      );
      console.log("userBranch", getUserBranch);
      const allTurnInHommework = getUserBranch.data.map(
        element => element.name
      );
      console.log(allTurnInHommework);
    }
  };

  useEffect(() => {
    getActualuUserData();
  }, [currentUser]);

  console.log(currentUser);
  const routing = useRouter(currentUser);
  console.log("rerender");
  return (
    <>
      {currentUser && (
        <>
          <h2>{currentUser.displayName}</h2>
          <img alt="avatar" src={currentUser.photoURL} />
        </>
      )}
      <Router>{routing}</Router>
    </>
  );
}

export default App;
